<template>
  <v-container
    id="regular-tables-view"
    fluid
    tag="section"
  >
    <material-card
      icon="mdi-clipboard-text"
      icon-small
      color="primary"
      title="Equipo"
    >
      <v-data-table
        :headers="headers"
        :items="lista_equipo"
        sort-by=""
        class="elevation-1"
        :loading="cargando"
        loading-text="Cargando... Espere por favor"
        no-data-text="No existen datos"
        no-results-text="No se encontraron registros coincidentes"
        :search="search"
        :footer-props="{
          showFirstLastPage: true,
          firstIcon: 'mdi-arrow-collapse-left',
          lastIcon: 'mdi-arrow-collapse-right',
          prevIcon: 'mdi-arrow-left',
          nextIcon: 'mdi-arrow-right',
          'items-per-page-text':'Items por página'
        }"
      >
        <template v-slot:top>
          <v-toolbar
            flat
          >
            <v-text-field
              v-model="search"
              append-icon="mdi-magnify"
              label="Búsqueda"
              single-line
              hide-details
            />
            <v-spacer />
            <v-btn
              color="primary"
              dark
              class="mb-2"
              to="/tables/equipo/form"
            >
              Nuevo Registro
            </v-btn>

            <v-dialog
              v-model="dialog"
              max-width="500px"
            >
              <v-card>
                <v-card-title>
                  <!-- <span class="text-h5">{{ formTitulo }}</span> -->
                  <span class="text-h5" style="color:darkblue">{{equipo.codigoActivo}} / {{ equipo.descripcion }}</span>
                  <span style="color:black">{{ordenador.Discos[0].type}}</span>
                </v-card-title>
                <v-card-text>
                  <!-- <v-form ref="form" v-model="valid"> -->
                    <v-container>
                      <v-row>
                        <span class="text-h4">Detalle Caracteristicas</span>
                        <v-col
                        v-for="(equipo, index) in equipo.DetalleEquipos" :key="index"
                          cols="12"
                          sm="12"
                          md="12"
                        >
                        <!-- <label style="color:red">{{equipo.descripcion}}: {{equipo.valor}}</label> -->
                        
                        <span style="color:black">{{equipo.descripcion}}: {{equipo.valor}}</span>
                        
                          <!-- <v-text-field
                            v-model="equipo.descripcion"
                            label="Descripción"
                            hint="Ejemplo: CPU, Impresora, Monitor, Teclado, etc."
                            :counter="50"
                            :rules="fieldRules"
                            :autofocus="true"
                            persistent-hint
                          /> -->
                        </v-col>
                      </v-row>
                      <v-row>
                        <v-col
                          cols="12"
                          sm="12"
                          md="12"
                        >
                          <!-- <v-autocomplete
                            v-model="equipo.idMarcas"
                            :items="lista_marca"
                            item-text="descripcion"
                            item-value="id"
                            label="Marcas"
                            multiple
                            chips
                            hint="Seleccione las marcas"
                            persistent-hint
                          >
                          </v-autocomplete> -->
                          <!-- {{equipo}} -->
                        </v-col>
                      </v-row>
                      
                    </v-container>
                  <!-- </v-form> -->
                </v-card-text>
                <v-card-actions>
                  <v-spacer />
                  <v-btn
                    class="ma-1"
                    
                    dark
                    
                    color="primary darken-3"
                    @click="close"
                  >
                    Cerrar
                  </v-btn>
                  <!-- <v-btn
                    :loading="estadoBoton"
                    text
                    @click="guardar"
                    :disabled="!valid"
                  >
                    Guardar
                  </v-btn> -->
                </v-card-actions>
              </v-card>
            </v-dialog>
            <v-dialog
              v-model="dialogDelete"
              max-width="500px"
            >
              <v-card>
                <v-card-title class="text-h5">
                  Está seguro de eliminar el registro?
                </v-card-title>
                <v-spacer />
                <v-card-subtitle class="text-h6">
                  {{ equipo.descripcion }}
                </v-card-subtitle>
                <v-card-actions>
                  <v-spacer />
                  <v-btn
                    color="blue darken-1"
                    text
                    @click="closeEliminar"
                  >
                    Cancelar
                  </v-btn>
                  <v-btn
                    color="red darken-1"
                    text
                    @click="eliminarConfirm"
                  >
                    Eliminar
                  </v-btn>
                </v-card-actions>
              </v-card>
            </v-dialog>
          </v-toolbar>
        </template>
        <template v-slot:[`item.acciones`]="{ item }">
          <v-btn
            class="ma-1"
            fab
            dark
            x-small
            color="primary darken-1"
            @click="editar(item)"
          >
            <v-icon
              dark
              small
              class="ma-2"
            >
              mdi-text
            </v-icon>
          </v-btn>
          <v-btn
            class="ma-1"
            fab
            dark
            x-small
            color="yellow darken-3"
            @click="editar(item)"
          >
            <v-icon
              dark
              small
              class="ma-2"
            >
              mdi-pencil
            </v-icon>
          </v-btn>
          <v-btn
            class="ma-1"
            fab
            dark
            x-small
            color="red darken-2"
            @click="eliminar(item)"
          >
            <v-icon
              dark
              small
              class="ma-2"
            >
              mdi-delete
            </v-icon>
          </v-btn>
        </template>
      </v-data-table>
    </material-card>
    <v-snackbar
      v-model="snackbar"
      :timeout="timeout"
      :color="color"
    >
      {{ text }}

      <template v-slot:action="{ attrs }">
        <v-btn
          color=""
          text
          v-bind="attrs"
          @click="snackbar = false"
        >
          Cerrar
        </v-btn>
      </template>
    </v-snackbar>
  </v-container>
</template>


<script>
import * as catService from "../../services/categoriaService"
import * as equService from "../../services/equipoService"
import * as marService from "../../services/distritoService"
import * as carService from "../../services/caracteristicaService"

  export default {
    data () {
      return {
        estadoBoton: false,
        dialog: false,
        dialogDelete: false,
        lista_equipo: [],
        lista_marca: [],
        lista_caracteristica: [],
        lista_categoria: [],
        cargando: true,
        equipo: {
          descripcion: '',
          idMarcas: []
        },
        search: '',
        headers: [
          {
            text: 'ID',
            align: 'start',
            sortable: false,
            value: 'id',
            
          },
          { text: 'Codigo', value: 'codigoActivo' },
          { text: 'Descripción', value: 'descripcion' },
          { text: 'Oficina', value: 'oficinaId' },
          { text: 'Fecha Alta', value: 'fechaAlta' },
          { text: 'Estado', value: 'estadoEquipo' },
          // { text: 'Caracteristica', value: 'descripcionCaracteristicas', divider: true },
          { text: 'Acciones', value: 'acciones', sortable: false, align: 'center' },
        ],
        editedIndex: -1,
        defaultEquipo: {
          descripcion: '',
          idMarcas: []
        },
        color: '',
        snackbar: false,
        text: '',
        timeout: 3000,
        valid: true,
        fieldRules: [
          v => !!v || 'El campo es requerido',
          v => (v && v.length <= 50) || 'El campo debe ser menor a 50 caracteres',
        ],

        ordenador: {Discos: [
          {
            id: null,
            type: ''
          }
        ]}
      }
    },
    computed: {
      formTitulo () {
        return this.editedIndex === -1 ? 'Nuevo Registro' : 'Editar Registro'
      },
    },
    watch: {
      dialog (val) {
        val || this.close()
      },
      dialogDelete (val) {
        val || this.closeEliminar()
      },
    },
    async mounted () {
      this.obtener()
      this.obtenerMarca()
      this.obtenerCaracteristicas()
    },
    methods: {

      async obtener () {
        const res = await equService.listar()
        // console.log(res)
        this.lista_equipo = res.data
        this.cargando = false
      },
      async obtenerMarca () {
        const res = await marService.listar()
        // console.log(res)
        this.lista_marca = res.data
        this.cargando = false
      },
      async obtenerCaracteristicas () {
        const res = await carService.listar()
        // console.log(res)
        this.lista_caracteristica = res.data
        this.cargando = false
      },
      async obtenerCategoria () {
        const res = await catService.listar()
        // console.log(res)
        this.lista_categoria = res.data
        this.cargando = false
      },


      async obtenerOrdenador () {
        const mod = this.lista_equipo[this.editedIndex]
        // const res = await equService.modificar(this.equipo, mod.id)
        const res = await equService.listarOrdenadorId(mod.id)
        console.log(res)
        this.ordenador = res.data
        console.log(this.ordenador);
        this.cargando = false
      },


      close () {
        this.dialog = false
        this.$nextTick(() => {
          this.equipo = Object.assign({}, this.defaultEquipo)
          this.editedIndex = -1
        })
        this.estadoBoton = false
      },
      async guardar () {
        let res = null
        this.validate();
        this.estadoBoton = true
        if (this.editedIndex > -1) {
          const mod = this.lista_equipo[this.editedIndex]
          res = await equService.modificar(this.equipo, mod.id)
          Object.assign(this.lista_equipo[this.editedIndex], this.equipo)
        } else {
          res = await equService.guardar(this.equipo)
          this.lista_equipo.push(this.equipo)
        }
        if(res.status == 200){
          this.text = "Registro guardado"
          this.snackbar = true
          this.color = "green"
        } else {
          this.text = res.data.message
          this.snackbar = true
          this.color = "dark-red"
        }
        this.obtener()
        this.estadoBoton = false
        this.close()
      },
      editar (item) {
        this.editedIndex = this.lista_equipo.indexOf(item)
        this.equipo = Object.assign({}, item)
        this.obtenerOrdenador()
        this.dialog = true
      },
      eliminar (item) {
        this.editedIndex = this.lista_equipo.indexOf(item)
        this.equipo = Object.assign({}, item)
        this.dialogDelete = true
      },
      async eliminarConfirm () {
        try {
          const mod = this.lista_equipo[this.editedIndex]
          const res = await equService.eliminar(mod.id)
          if(res.status == 200){
            this.text = res.data.message
            this.snackbar = true
            this.color = "green"
            this.lista_equipo.splice(this.editedIndex, 1)
          } else {
            this.text = res.data.message
            this.snackbar = true
            this.color = "dark-red"
          }
          this.closeEliminar()
        } catch (error) {
          console.log(error);
        }
      },
      closeEliminar () {
        this.dialogDelete = false
        this.$nextTick(() => {
          this.equipo = Object.assign({}, this.defaultEquipo)
          this.editedIndex = -1
        })
      },
      validate() {
        this.$refs.form.validate()
      }
    },
  }
</script>
